@import "vars";
@mixin scroll {
    &::-webkit-scrollbar {
        width: 5px;
    }
    &::-webkit-scrollbar-track {
        background: #F3F6F9;
        border-radius: 5px;
    }
    &::-webkit-scrollbar-thumb {
        background: rgba(#85949E, 0.5);
        border-radius: 5px;
    }
}